export const purchaseFootballClip =

    `
import FootballClipNFT from 0xc8af9ee840bc6aab
import NonFungibleToken from 0x631e88ae7f1d7c20
import Marketplace from 0xc8af9ee840bc6aab
import FlowToken from 0x7e60df042a9c0868
import FungibleToken from 0x9a0766d93b6608b7

transaction(account:Address,id:UInt64) {

    
prepare(acct: AuthAccount) { //acct - account that calls the transaction
   

        let saleCollection = getAccount(account).getCapability(/public/MySaleCollection)
     .borrow<&Marketplace.SaleCollection{Marketplace.SaleCollectionPublic}>()
                                                             ?? panic ("This collection does not exist here")

        let recepientCollection = getAccount(acct.address).getCapability(FootballClipNFT.CollectionPublicPath)
     .borrow<&FootballClipNFT.Collection{FootballClipNFT.CollectionPublic}>()
                                                             ?? panic ("This collection does not exist here")  

        let price=saleCollection.getPriceOfFootballClip(id:id);
        let payment <- acct.borrow<&FlowToken.Vault>(from:/storage/flowTokenVault)!.withdraw(amount: price*0.95) as! @FlowToken.Vault   
        let paymentToDeveloper <- acct.borrow<&FlowToken.Vault>(from:/storage/flowTokenVault)!.withdraw(amount: price*0.05) as! @FlowToken.Vault 

      saleCollection.purchaseFootballClip(recepientCollection:recepientCollection,payment:<-payment, id:id,paymentToDeveloper:<-paymentToDeveloper)
  
 }  

}
    `